<template>
    <div  class="info-div" style="100%">
        <div class="info-header">
          <span class="callback" @click="resetComponent">充电状态监测 </span>
          <span> > 设备详情</span>
        </div>
        <div class="info-container" >
            <div  class="list-content detail-content  list-contentB" >
              <div class="list-header " style="border:0">
                 <i class="hasIcon hasImg">
                  <img src="dist/img/widsomApp/settings.png" />
                </i>设备详细信息
                 <button @click="init" class="list-operate" >
                    <i  class="hasIcon " ><icon name="refresh" ></icon></i>
                    刷新
                </button>
                 <button @click="disabledDevice" class="list-operate" v-if="deviceCommInfo.runState != 2 && controls[42]">
                    <i  class="hasIcon " ><icon name="ban" ></icon></i>
                    停充
                </button>
                 <button @click="heartData(true)" class="list-operate" v-if="deviceCommInfo.runState == 2 || true">
                    <i  class="hasIcon " ><icon name="heartbeat" ></icon></i>
                    心跳
                </button>
              </div>
              <div class="commInfoL">
                 <div class="oneInFour">
                     <span class="titleS">剩余时间（M）</span>
                     <div class="contentS ">  <div class="b">{{deviceCommInfo.remainTime.toString()}}</div></div>
                 </div>
                 <div class="oneInFour">
                      <span class="titleS">电压（V）</span>
                      <div class="contentS ">
                         <div class="y">{{deviceCommInfo.voltage}}</div>
                      </div>
                 </div>
                 <div class="oneInFour">
                      <span class="titleS">电流（A）</span>
                      <div class="contentS ">   <div class="o">{{deviceCommInfo.current}}</div></div>
                 </div>
                 <div class="oneInFour">
                      <span class="titleS">功率（W）</span>
                      <div class="contentS ">   <div class="r">{{deviceCommInfo.power}}</div></div>
                 </div>
                 <div class="oneInFour">
                      <span class="titleS">电量（KWH）</span>
                      <div class="contentS ">   <div class="r">{{deviceCommInfo.usePower}}</div></div>
                 </div>
              </div>
               <div class="commInfoR">
                  <ul class="form-list">
                     <li v-for="(item,$index) in commInfo"  style="margin:0!important;width:calc(32%)">
                         <span class="label-span">{{item.text}}</span>
                         <span class="label-span" style="max-width: calc(100% - 8em);" :title="item.value"> &nbsp;:&nbsp;&nbsp;{{item.value}}</span>
                     </li>
                  </ul>
              </div>
            </div>
            <div  class="list-content detail-content  bottomList list-contentB " >
              <div class="list-header" style="border:0">
                 <i class="hasIcon hasImg">
                  <img src="dist/img/widsomApp/settings.png" />
                </i>设备统计曲线
              </div>
              <div class="chartCon">
                  <div class="oneInThree">
                     <div class="chart" ref="powerChart"></div>
                     <p>
                        <i class="hasIcon hasImg">
                           <img src="dist/img/widsomApp/power.png" />
                        </i>功率曲线
                      </p>
                  </div>
                  <div class="oneInThree">
                     <div class="chart" ref="voltageChart"></div>
                     <p>
                         <i class="hasIcon hasImg">
                           <img src="dist/img/widsomApp/electric_current.png" />
                        </i>电流曲线
                     </p>
                  </div>
                  <div class="oneInThree">
                     <div class="chart" ref="currentChart"></div>
                     <p>
                       <i class="hasIcon hasImg">
                           <img src="dist/img/widsomApp/voltage.png" />
                        </i>电压曲线
                     </p>
                  </div>
              </div>
            </div>
        </div> 
        <dialogs type="customer" width="60em" maxHeight="620px" ref="heartDataDia">
           <div slot="header">
              心跳列表
           </div>
           <div slot="content">
            <!-- @freshStaticTable="heartData(false)"-->
              <tables ref="caution"  class="dialogTable" :tableOpt="TableOpt" v-if="showDialogTable" @freshStaticTable="heartData(false)"></tables>
           </div>
        </dialogs>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="confirm" ref="confirms"></dialogs>
        <dialogs type="loading" ref="loadings"></dialogs>
    </div>
</template>
<script>
    import 'vue-awesome/icons/ban'
    import 'vue-awesome/icons/heartbeat'
    
    export default{
        data() {
            return {
               showDialogTable: false,
               TableOpt:{
                   column: [
                        {title: '时间', width: '140', field: 'heartTime', textAlign: 'center'},
                        // {title: '端口', width: '120', field: 'port', textAlign: 'center'},
                        {title: '状态', field: 'state', textAlign: 'center',  width: 120,type: 'string', tips: 'eventContent',
                         formatter: function (val, index, row, obj) {
                           let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                              switch(vals) {
                                  case '0':
                                      return `<div class="green">正常</div>`
                                  case '1':
                                  //${row.eventContent}
                                      return `<div class="red">告警</div>`
                                  case '-1':
                                      return `<div class="orange">充电结束</div>`
                                   default: 
                                      return `<div>--</div>`   
                                  }
                            
                          }
                        },
                        {title: '电压', width: '120', field: 'voltage', textAlign: 'center'},
                        {title: '电流', width: '120', field: 'current', textAlign: 'center'},
                        {title: '功率', width: '120', field: 'power', textAlign: 'center'},
                        {title: '电量', width: '120', field: 'activeTotal', textAlign: 'center'},
                        {title: '剩余时间(分钟)', width: '160', field: 'remainTime', textAlign: 'center',type: 'string',formatter: function (val, index, row, obj) {
                             if(typeof val !== 'undefined') {
                               return val.toString()
                             }
                             return '--'
                          }
                        } 
                    ],
                   // data: [],
                    url: '',
                    method: 'post',
                    hasRefresh: true,
                    queryParam: {},
                   // noFooter: true,
                    pageSize: 50,
                    pageNumber: 1,
                    height: 300
               },
               deviceCommInfo: {
                  runState: '',
                  deviceNo: '',
                  useTime: '',
                  customerName: '',
                  carCategory: '',
                  remainElectric: '',
                  remainAount: '',
                  remainTime: '',
                  voltage: '',
                  current: '',
                  power: '',
                  eventContent: ''
               },
               lineV: '',
               lineA: '',
               lineR: '',
               lineData: {
                 xData: [],
                 yPowerData: [],
                 yCurrentData: [],
                 yVoltageData: []
               }
            }
        },
        props: ['row', 'controls'],
        methods: {
            heartData: function(flag) {
               if(this.deviceCommInfo.runState == 1) {
                   if(this.showDialogTable) {
                      this.$refs.caution.refreshTable(null, 1);
                   } else {
                      this.TableOpt.queryParam.chargingPlieGuid =  this.row.chargingPlieGuid;
                      this.TableOpt.queryParam.chargingGuid =  this.deviceCommInfo.chargingGuid;
                      this.TableOpt.url = 'charger/queryHeartBychargingGuid';
                      this.showDialogTable = true;
                   }
                   this.showHeartData();
               } else {
                   let that = this;
                    that.showDialogTable = true;
                    that.getRequest({
                      url: 'charger/queryHeartList',
                      method: 'post',
                      param: {
                        chargingPlieGuid: that.row.chargingPlieGuid
                      },
                      success: function (data) {
                        that.$refs.caution.resetTableOpt({url: '', data: data.data, method: ''});
                        if(flag) {
                           that.showHeartData();
                         }
                      },
                      error: function () {
                        that.$refs.caution.resetTableOpt({url: '', data: []});
                        if(flag) {
                          that.showHeartData();
                        }
                      }
                    }, true)
               }
            },
            showHeartData: function () {
              this.$refs.heartDataDia.show({
                  hasClose: true
              })
            },
           //返回列表
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
            getDeviceInfo: function (flag) {
              
              let that = this;
           
              that.getRequest({
                url: 'charger/monitorDeviceList',
                method: 'post',
                param: {
                  chargingPlieGuid: that.row.chargingPlieGuid,
                  pageSize: 1,
                  pageNumber: 1
                },
                success: function (data) {
                   let Data = data.data[0];
                   for(let key in Data) {
                      that.$set(that.deviceCommInfo, key, Data[key]);
                   }
                   if(!flag) {
                     that.getLineData();
                   }
                  
                }
              })
            
            },
            //电压折线图
            getLineC: function () { 
               this.lineV  = echarts.init(this.$refs.currentChart);
               this.getLine(this.lineV, ['rgba(22,90,162, 1)', 'rgba(22,90,162, 0.1)'], ['电压', 'V'], 'yVoltageData', 'popup_blue')
            },
            //电流折线图
            getLineV: function () {
               this.lineA  = echarts.init(this.$refs.voltageChart);
               this.getLine(this.lineA, ['rgba(192,64,89, 1)', 'rgba(192,64,89, 0.1)'], ['电流', 'A'], 'yCurrentData', 'popup_red')
            },
            //功率折线图
            getLineR: function () {
               this.lineR = echarts.init(this.$refs.powerChart);
               this.getLine(this.lineR,  ['rgba(184,193,62, 1)', 'rgba(184,193,62, 0.1)'], ['功率', 'W'], 'yPowerData', 'popup_yellow')
            },
            getLine: function (obj, color, typeText, key, img) {
               let option = {
                     toolbox: {
                        show: false,
                        feature: {
                          dataZoom: {
                            yAxisIndex: 'none'
                          },
                          restore: {},
                          saveAsImage: {}
                        }
                      },
                      grid: {
                        left: 40,
                        right: 30,
                        bottom:40,
                        top: 50
                      },
                      xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['1月' ,'2月', '3月', '4月', '5月', '6月', '7月','8月', '9月', '10月','11月','12月'],
                        nameTextStyle: {
                          color: 'rgba(255,255,255, 0.1)',
                        },
                        axisLabel: {
                          color: 'rgba(255,255,255, 0.1)',
                          textStyle: {
                            color:'rgba(255,255,255, 1)'
                          }
                        },
                        splitLine: {
                          show:true,
                          lineStyle: {
                            color: 'rgba(255,255,255, 0.1)',
                            type: 'dotted',
                            width: 2
                          }
                        },
                        axisLine: {
                          show:true,
                          lineStyle: {
                            color: 'rgba(255,255,255, 0.1)'
                          
                          }
                        }
                      },
                      yAxis: {
                        name: typeText[0] + ' ( ' + typeText[1] + ' ) ',
                        type: 'value',
                        nameTextStyle: {
                          color: 'rgba(255,255,255, 1)',
                        },
                        axisLabel: {
                          color: 'rgba(255,255,255, 0.1)',
                          textStyle: {
                            color: 'rgba(255,255,255, 1)'
                          }
                        },
                        axisLine: {
                          show:true,
                          lineStyle: {
                            color: 'rgba(255,255,255, 0.1)'
                          }
                        },
                        splitLine: {
                          show:true,
                          lineStyle: {
                            color: 'rgba(255,255,255, 0.1)',
                            width: 2,
                            type: 'dotted'
                          }
                        }
                      },
                      tooltip : {
                        trigger: 'axis',
                        axisPointer: {
                          type: 'cross'
                        }
                      },
                      series: 
                        {
                          name: typeText[0],
                          type:'line',
                          smooth:false,
                          symbol: "circle", 
                          symbolSize: 8,
                         // sampling: 'max',
                          itemStyle: {
                           normal: {
                               color: color[0]
                             }
                           },
                          markPoint: {
                          // symbol: "pin",
                           data: [
                              {
                                type: 'max', 
                                name: '最大值',
                                symbol: 'image://dist/img/widsomApp/'+img+'.png',
                                symbolOffset: [0, '-55%'],
                                label: {
                                 normal: {
                                    padding: [5,10,15,10],
                                    color: '#fff',
                                    position: 'inside'
                                  }
    
                                }
                              },
                              {
                                type: 'min', 
                                name: '最小值',
                                symbol: 'image://dist/img/widsomApp/'+img+'.png',
                                symbolOffset: [0, '-55%'],
                                label: {
                                 normal: {
                                    padding: [5,10,15,10],
                                    color: '#fff',
                                    position: 'inside'
                                  }
    
                                }
                              }
                           ]
                          },
                          areaStyle: {
                            normal: {
                              color:  new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 0,
                                color: color[0]
                              }, {
                                offset: 1,
                                color: color[1]
                              }])
                            }
                          },
                          data: []
                        }
               };
               option.xAxis.data  = this.lineData.xData;
               option.series.data  = this.lineData[key];
               obj.setOption(option, true);
            },
            getLineData: function () {
                let that = this;
                 //'charger/deviceCurve'
                that.getRequest({
                 url: 'charger/queryCurve',
                 method: 'post',
                 param: {
                   chargingPlieGuid: that.row.chargingPlieGuid,
                   chargingGuid: that.deviceCommInfo.runState == 1 ? that.deviceCommInfo.chargingGuid : ''
                   //startTime: that.row.startTime
                 },
                 success: function (data) {
                    let Data = data.data;
                    that.lineData.xData = Data.xData || [];
                    that.lineData.yPowerData = Data.yPowerData || [];
                    that.lineData.yCurrentData = Data.yCurrentData || [];
                    that.lineData.yVoltageData = Data.yVoltageData || [];
                    that.getLineC();
                    that.getLineV();
                    that.getLineR();
                    if(that.deviceCommInfo.runState.toString() == 0) {
                  
                      let len1 = that.lineData.yPowerData.length;
                      let len2 = that.lineData.yCurrentData.length;
                      let len3 = that.lineData.yVoltageData.length;
                  
                      that.$set(that.deviceCommInfo, 'current', len2 > 0 ? that.lineData.yCurrentData[len2 - 1] : '0');
                      that.$set(that.deviceCommInfo, 'voltage', len3 > 0 ? that.lineData.yVoltageData[len3 - 1] : '0');
                      that.$set(that.deviceCommInfo, 'power', len1 > 0 ? that.lineData.yPowerData[len1 - 1] : '0')
                    }
                 }
               }, true)
            }, 
            deviceUpdate: function () {
               let that = this;
              // alert(1)
               that.getWebsocket({
                 sign: true,
                 keyMsg: 'monitorDeviceList',
                 funMsg: function (data) {
                    let datas = data.data;
        
                    that.compareData(datas)
                 }
               });
                //console.log(that.$store.state.callback['monitorDeviceList'].success)
            },
            compareData: function(obj) {
               if(obj.deviceNo == this.row.deviceNo && this.row.port == obj.port) {
                  //console.log(1)
                  if(this.deviceCommInfo.runState.toString() != 1 && obj.runState == 1) {
                     this.getDeviceInfo(true);
                  }
                  for(let key in obj) {
                     this.$set(this.deviceCommInfo,key,obj[key] === null ? '--' : obj[key]);
                  }
                  this['lineData'].xData.push(obj.time);
                  this['lineData'].yVoltageData.push(obj.voltage);
                  this['lineData'].yCurrentData.push(obj.current);
                  this['lineData'].yPowerData.push(obj.power);
                  if(!this.lineA) {
                     return
                  }
                  this.lineA.setOption({
                       xAxis: {
                          data: this['lineData'].xData
                       },
                       series: {
                           data: this['lineData'].yCurrentData
                       }
                  });
                  if(!this.lineV) {
                     return
                  }
                  this.lineV.setOption({
                       xAxis: {
                          data: this['lineData'].xData
                       },
                       series: {
                           data: this['lineData'].yVoltageData
                       }
                  });
                  if(!this.lineR) {
                     return
                  }
                 this.lineR.setOption({
                       xAxis: {
                          data: this['lineData'].xData
                       },
                       series: {
                           data: this['lineData'].yPowerData
                       }
                  });
               }
            },
            disabledDevice: function () {
               let that = this;
               let alerts = this.$refs.alerts;
               let param = {};
               param.commNo = this.row.commNo;
               param.port = this.row.port;
               param.onOrOff = -1;
               this.$refs.confirms.show({
                  msg: '确定要停止充电吗？',
                  action: {
                     confirm: function () {
                         that.getRequest({
                             url: 'charger/offOn',
                             param: param,
                             method: 'post',
                             success: function (data) {
                               that.$refs.loadings.show({msg: '正在停止，请稍等。。。', hasClose: true});
                               that.stopDeviceAcc();
                             },
                             error: function (data) {
                               alerts.show({msg: data.message})
                             }
                          })
                     }
                  }
               })
            },
            stopDeviceAcc: function () {
               let that = this;
               let alerts = this.$refs.alerts;
               that.getWebsocket({
                 keyMsg: 'onOffDevice',
                 funMsg: function (data) {
                    if(that.row.deviceNo == data.data.deviceNo && that.row.port == data.data.port) {
                       let datas = data.data.status;
                       let msg = '';
                       if(datas == 1) {
                            msg = '操作成功';
                            that.getDeviceInfo(true);
                            that.stopAcc();
                       } else if(datas == 0) {
                            msg = '操作失败'
                       } else if(datas == -1) {
                            msg = '设备不在线'
                       }
                       that.$refs.loadings.close();
                       alerts.show({msg: msg});
                       that.$store.state.callback['onOffDevice'] = null;
                       that.$store.state.linkCheck = 'linkCheck';
                    }
                 }
               });
            },
            stopAcc: function () {
               let that = this;
               that.getWebsocket({
                 keyMsg: 'onOffDevice',
                 sign: true,
                 funMsg: function (data) {
                    let datas = data.data;
                    if(datas.deviceNo == that.row.deviceNo && datas.port === that.row.port) {
                       that.getDeviceInfo(true);
                     }
                 }
               })
            },
            init: function () {
                 this.getDeviceInfo();
                 this.stopAcc();
                 this.deviceUpdate();
            }
        },
        computed: {
          commInfo(){
     
             return [
                {text: '设备编号', value: this.deviceCommInfo.deviceNo || '--'},
                {text: '端口号', value: this.deviceCommInfo.port || '--'},
                {text: '使用人员', value: this.deviceCommInfo.runState == 1 ? (this.deviceCommInfo.customerName || '--') : '--'},
                {text: '开始时间', value: this.deviceCommInfo.runState == 1 ? (this.deviceCommInfo.startTime || '--') : '--'},
                {text: '充电时长', value: this.deviceCommInfo.runState == 1 ?  (this.deviceCommInfo.useTime.toString() + '（分钟）' || '--') : '--'},
                {text: '剩余时间', value: this.deviceCommInfo.runState == 1 ?  (this.deviceCommInfo.remainTime.toString() + '（分钟）' || '--') : '--'},
                {text: '设备状态', value: this.$parent.getState(this.deviceCommInfo.runState, this.deviceCommInfo.online).text || '--'},
                {text: '用户类别', value: this.deviceCommInfo.runState == 1 ? (this.deviceCommInfo.carCategory == 1 ? '电瓶车' : (this.deviceCommInfo.carCategory == 2 ? '电动车' : '--')): '--' },
                {text: '充电方式', value: this.deviceCommInfo.runState == 1 ? (this.getEnumItem('payCategory', this.deviceCommInfo.payCategory).text) : '--' }
                
             ]
          }
        },
    
        mounted: function () {
           this.init();
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
    $commInfoH: 8em;
    .commInfoL{
      width: 55%;
      height: $commInfoH;
      float:left;
      display:flex;
      .oneInFour{
        flex: 1;
        margin-top: 1em;
        height:calc(100% - 1em);
        float:left;
        margin: 1em 0.5em 0 0.5em;
        span.titleS{
            text-align:center;
            display:block;
            font-size: 0.9em;
            width:100%;
            font-size:0.9em;
            line-height:2
        }
        div.contentS{
           display:table;
           width:100%;
           background: url('../../../../dist/img/widsomApp/square.png') no-repeat;
           height: calc(100% - 2.5em);
           background-position: center 40%;
           background-size: contain;
           div{
            display: table-cell;
            font-size: 2.4em;
            font-weight:600;
            vertical-align: middle;
           }
           div.b{
            color: #278FE4
           }
           div.y{
            color: #F3C265
           }
           div.o{
            color: #E89839
           }
           div.r{
            color: #FF4253
           }
        }
      }
    }
    .commInfoL:after{
        @extend .clearElement
    }
    .commInfoR{
      width: 40%;
      margin-left: 5%;
      font-size:1.05em;
      height: $commInfoH;
      float:left;
    }
    .bottomList{
      height:calc(100% - 14em);
      .chartCon{
          display:flex;
          width:100%;
          height:calc(100% - 3em);
         .oneInThree{
             //flex: 1;
             width: calc(100% / 3 - 1.6em);
             height:100%;
             margin:0 0.8em;
             .chart{
                height: calc(100% - 3em);
                width:100%
             }
             p{
               font-size: 0.9em;
               width: -webkit-fit-content;
               width: -moz-fit-content;
               width: fit-content;  
               margin: auto;
             }
          }
      }
    
    }
    
</style>



