<template>
	<div ref="trys" style="height: 100%">
      <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" ref="infoDetail"></info>
      </div>
   	  <div  v-show="!showInfo"  class="all-container">
   	  	<div class="operate-div">
            <template v-for="(item, $index) in formList">
              <!--   <selects ref="selects" v-if="item.type == 'select'" :option="item.options" @getSelect="getFrom"></selects> -->
                <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs>
            </template>
           <!--  <button @click="getAllData" style="float:left" class="button-operate">
                <i  class="hasIcon hasImg" ><icon name="refresh" scale="1.2"></icon></i>
                刷新
            </button>
            <i @click="downLoadData" class="hasIcon " style="margin-top:0.7em;cursor:pointer"><icon name="download" scale="1.4"></icon></i> -->
         </div>
         <div class="deviceCon">
         	<ul class="deviceTab">
         		<li v-for="(item, $index) in tabs"  :class="{'selectTabLi': tabIndex === $index}" @click="getTab($index, item)">    
         			<i class="hasIcon hasImg">
         				<img :src="'dist/img/widsomApp/' + item.img + '.png'">
         			</i>                                                  
         			{{item.tabName}} <span class="hasWeight" :class="item.colors"> ( {{item.num}} ) </span>
         		</li>
            <li class="rightBtnLi">
                <button class="list-operate" @click="getAllData" >
                    <i  class="hasIcon " ><icon name="refresh" ></icon></i>
                    刷新
                </button>
                <span class="splitSpan" v-if="controls[6]"></span>
                <button class="list-operate" @click="downLoadData" v-if="controls[6]">
                    <i  class="hasIcon " ><icon name="download" ></icon></i>
                    下载
                </button>
            </li>
         	</ul>
         	<VuePerfectScrollbar class="tabCon">
         	   <div class="autoTab" v-if="tabData.length > 0">	
         		  <div v-for="(item, $index) in tabData" class="deviceDiv" @click="getInfoDetail(item)" :title="item.deviceNo" >
         			  <div class="content" :class="{inDanger: item.runState == -1, noUse:item.runState == 2 }">
         			  	 <img :src="'dist/img/widsomApp/' + getState(item.runState, item.online).img + '.png'" :class="{centerImg: item.runState != 1}" v-if="item.runState != 1"  />
         			  	 <div v-if="item.runState == 1" class="hasPercent" >
                            <div class="imgLeft" :style="{width: item.percent + '%'}"></div>
                            <div class="imgRight" :style="{width: 'calc(100% - ' + (item.percent + '%') + ')', background: item.online == 1 ? 'white' : 'rgba(255,255,255,0.6)'}"></div>
                            <div class="imgTop" :class="{imgTopDDC: item.carCategory == 2}"></div>
         			  	 </div>
         			  	 <span class="centerSpan">
         			  	   	{{ getState(item.runState, item.online).text}}
         			  	 </span>
         			  	 <span v-if="item.runState == 1 ">
         			  	  	剩余时间  {{item.remainTime}}分钟
         			  	 </span>
                   <!-- <span v-if="item.runState == 1 && item.payCategory == 2">
                      包月充值
                   </span> -->
         			  </div>
         			  <p >{{item.deviceNo}} - {{item.port}}</p>
         		  </div>
         		</div>
         	</VuePerfectScrollbar>
         </div>
   	  </div>
   </div>
</template>

<script>
   import info from './info.vue'
   export default{
     data() {
       return {
          controls:[],
          row: '',
          showInfo: false,
       	  currentTab: '',
          tabIndex: 0,
          tabData: [],
          deviceAccount: {
          	total: 0,
          	onTotal: 0,
          	freeTotal: 0,
          	errorTotal: 0,
          	offTotal: 0,
          	outLine: 0,
            offlineTotal: 0
          },
          formData: {
            payCategory: '',
            deviceNo: ''
          }
       }
     },
     components: {
       info
     },
     computed: {
     	  formList: function () {
            return [
               {
                 type: 'select',
                 options: {
                     width: '10em',
                     cs: 'server',
                     method: 'post',
                     url: 'dictItem/queryByDictCode',
                     param: {dictCode: '18'},
                     hasAll: true,
                     text: 'dictItemName',
                     value: 'dictItemValue',
                     key: 'formData.payCategory'
                 }
               },
               {
                 type: 'input',
                 options: {
                   type: 'typeSearch',
                   key: 'formData',
                   selectOpt: {
                     width: '8em',
                     cs: 'client',
                     data: [
                      {text: '设备编号', value: 'deviceNo'}
                     ]
                   }
                 }
               }
            ]
      	},
      	tabs: function () {
     	    return [
            {tabName: '所有设备', num: this.deviceAccount.total, colors: "b", img: 'total',  state: '', online: ''},
            {tabName: '充电', num: this.deviceAccount.onTotal, colors: "y", img: 'active', state: '1', online: '1'},
            {tabName: '空闲', num: this.deviceAccount.freeTotal, colors: "o", img: 'free',  state: '0', online: '1'},
            {tabName: '异常', num: this.deviceAccount.errorTotal, colors: "r", img: 'alarm', state: '-1',online: '1'},
            {tabName: '停运', num: this.deviceAccount.offTotal, colors: "", img: 'down', state:'2', online: '1'},
            {tabName: '离线', num: this.deviceAccount.offlineTotal, colors: "y1", img: 'outline', state: '', online: 0}
     	    ]
     	 }
     },
     methods: {
       getAllData: function () {
          this.getStateAccount();
          this.getDeiceData();
          this.deviceUpdate();
          this.stopAccs();
       },
       deviceUpdate: function () {
           let that = this;
           that.getWebsocket({
              keyMsg: 'monitorDeviceList',
              sign: true,
              funMsg: function (data) {
                let datas = data.data;
                that.compareData(datas)
              }
           });
           //console.log(that.$store.state.callback['monitorDeviceList'].success)
       },
       compareData: function (obj) {
         let Data = this.tabData;
         let sign = this.formData.deviceNo == '';
         if(this.formData.deviceNo != '') {
              if((obj.deviceNo).indexOf(this.formData.deviceNo) > -1) {
                   sign  = true
               }
         }
         if(sign) {
              for(let i = 0; i<Data.length; i++) {
                let item = Data[i];
                if(this.currentTab.state == '') {
                      let f = this.currentTab.online.toString() == '0' && obj.online.toString() == item.online.toString();
                      let flag = this.currentTab.online.toString() == '' ;
                      if(obj.deviceNo == item.deviceNo && item.port == obj.port && (flag || f)) {
                         for(let key in obj) {
                          this.$set(this.tabData[i], key, obj[key])
                         }
                        // this.getStateAccount();
                         return
                       } else if(this.currentTab.online.toString() == '0' && obj.online.toString() != item.online.toString() && obj.deviceNo == item.deviceNo && item.port == obj.port) {
                         this.tabData.splice(i,1);
                         this.getStateAccount();
                         return
                       } else if(i == (Data.length - 1) && (flag || (this.currentTab.online.toString() == '0' && obj.online.toString() == '0'))) {
                          this.tabData.unshift(obj);
                          this.getStateAccount();
                         return
                       } 
                      
                
                } else if(obj.online == 1 ){
                  if(obj.runState.toString() == this.currentTab.state && obj.deviceNo == item.deviceNo && item.port == obj.port) {

                     for(let key in obj) {
                       this.$set(this.tabData[i], key, obj[key])
                     }
                     return
                  }  else if(obj.runState.toString() != this.currentTab.state && obj.deviceNo == item.deviceNo && item.port == obj.port) {
                     this.tabData.splice(i,1);
                     this.getStateAccount();
                     return
                  } else if(obj.runState.toString() == this.currentTab.state && (!(obj.deviceNo == item.deviceNo && item.port == obj.port)) && (i == (Data.length - 1))) {
                     this.tabData.unshift(obj);
                     this.getStateAccount();
                     return
                  }
                } 
            }
         }
        
       },
     
       getStateAccount: function () {
          let that = this;
         // for(let key in this.deviceAccount) {
              //this.deviceAccount[key] = 0
         // }
          that.getRequest({
              url: 'charger/monitorDevice',
              method: 'post',
              param: that.getSearchParam(false),
              success: function (data) {
                  let Data = data.data;
                  for(let key in that.deviceAccount) {
                     that.$set(that.deviceAccount, key , Data[key] || 0)
                  }
              }
          })
       },
       closeDiv: function () {
         this.showInfo = false;
         this.getAllData();
        // this.getStateAccount()
       },
       getInfoDetail: function (row) {
         this.row = row;
         this.showInfo = true;
       },
       getFrom: function (val) {
        if (this.formData[val.key.split('.')[1]] !== val.val){
           if (val.combine) {
                this.formData = this.cleanSearchData(this.formData, ['payCategory']);
           }
           this.getOption(val, true);
           this.getStateAccount();
           this.getDeiceData();
        }
     	 
       },
       getState: function (state, online) {
          if(online != 1) {
            return {
                 text: '离线',
                 img: 'outline_icon'
            };
          } 
          let s = typeof state !== 'undefined' || state == 'undefined' ? state.toString() : '';
          switch(s) {
          	case '1': 
          	  return {
          	  	 text: '充电',
          	  	 img: 'ddcs'
          	  };
          	case '0': 
          	  return {
          	  	 text: '空闲',
          	  	 img: 'free_icon'
          	  };
          	case '-1': 
          	  return {
          	  	 text: '异常',
          	  	 img: 'alarm_icon'
          	  };
          	case '2': 
          	  return  {
          	  	 text: '停运',
          	  	 img: 'forbidon'
          	  };
          	default: {
             return  {
                 text: '',
                 img: ''
              };
            }
          }
       },	
       getTab: function (index, tab) {
          this.tabIndex = index;
          this.currentTab = tab;
          this.getAllData();
       },
       getSearchParam: function (flag) {
          let param = this.formData;
        
          if(flag) {
             param.runState =  this.currentTab.state;
             param.online = this.currentTab.online;
          } else {
            param.runState = ''
          }
          param.pageSize = 999999999;
          param.pageNumber = 1;
          param = this.getSearchData(param);
          return param;
       },
       downLoadData: function () {
         this.downLoad({
            url: 'charger/monitorDeviceListDown',
            data: this.getSearchParam(true)
         })
       },
       getDeiceData: function () {
       	  this.tabData = [];
          let that = this;
   
          that.getRequest({
          	url: 'charger/monitorDeviceList',
          	method: 'post',
          	param: that.getSearchParam(true),
          	success: function (data) {
          	  that.tabData = data.data
          	}
          })
       },
       resizeCon: function () {
          this.getHeight();
          if (this.$refs.infoDetail) {
              let info = this.$refs.infoDetail;
              info.lineR !== ''?  info.lineR.resize() : '';
              info.lineA !== ''?  info.lineA.resize() : '';
              info.lineV !== ''?  info.lineV.resize() : '';
            }
      },
      windowChange: function () {
          this.resizeCon();
          // let that = this;
          // window.onresize = function () {
          //   that.resizeCon();
          // }
      },
      stopAccs: function () {
          let that = this;
          that.getWebsocket({
             keyMsg: 'onOffDevice',
             sign: true,
             funMsg: function (data) {
                let datas = data.data;
                  if(datas.status == 1) {
                       that.compareData(datas);
                     }
                 }
               })
      },
      init: function () {
          this.currentTab = this.tabs[0];
          this.getAllData()
          let that = this;
         // that.deviceUpdate();
         /*  window.onresize = function () {
             that.windowChange()
          }*/
      }
     },
     created: function () {
       this.isIn();
       this.getAction();
     },
     beforeRouteEnter(to, from, next) {
        next(vm => { 
            if(vm.showInfo) {
              let obj = vm.$refs.infoDetail;
              obj.init();
            } else {
              vm.getAllData();
            }
       });
     },
     watch: {
     	 $route(to, from) {
        // this.getAllData()
       }
     }
    }
</script>

<style lang="scss">
    $deviceDivWidth: 7.64em;
   .deviceCon{
   	 height: calc(100% - 6.5em);
   	 float:left;
   	 width:100%;
   	 font-size:0.94em;
   	 ul.deviceTab{
        text-align:left;
   	 	li{
   	 		position:relative;
   	 		padding:0.7em 2%;
   	 		display:inline-block;
   	 		font-size:0.94em;
   	 		min-width: 8%;
   	 		text-align:center;
   	 		cursor:pointer;
   	 		margin-right:2px;
   	 		line-height:2.4;
   	 		span.hasWeight{
   	 		     font-weight: 600
   	 	    }
   	 	  .hasImg{
   	 	    	width: 2.4em;
   	 	    	height:2.4em;
   	 	    	margin-right:1em
   	 	  }
   	 	}
   	 	li:before{
   	 		content: '';
   	 		display: block;
   	 		position:absolute;
            background-color: rgba(255,255,255,0.6);
            width:2px;
            height:80%;
            left:0;
            top:10%
   	 	}
   	 	li:hover, li.selectTabLi{
   	 		background: rgba(34, 127, 223, 0.21);
   	 		border-top-left-radius:0.4em;
   	 		border-top-right-radius:0.4em
   	 	}
      li:first-child:before, li:hover:before, li.selectTabLi:before, li:hover + li:before,  li.selectTabLi +  li:before, li.rightBtnLi:before{
        	width:0
      }
      li.rightBtnLi{
         float:right;
         padding:0.7em;
         .splitSpan{
           left:50%;
           background: rgba(255,255,255,0.6)
         }
      }
      li.rightBtnLi:hover{
        background:transparent;
      }
   	 	span.b{
          color: #278FE4
        }
        span.y{
          color: #F3C265
        }
        span.o{
          color: #E89839
        }
        span.r{
          color: #FF4253
        }
        span.y1{
          color: #E18038
        }
        span.otherO{
        	color: #E2803B
        }
   	 }
   	 .tabCon{
   	 	height: calc(100% - 3.5em);
   	 	background: rgba(34, 127, 223, 0.21);
   	 	width:calc(100% - 0em);
   	 	margin-top:1px;
   	 	padding:0 0em;
   	 	 position: relative;
   	 	border-bottom-left-radius:0.2em;
   	 	border-bottom-right-radius:0.2em;
   	 	.autoTab{
   	 		width: -webkit-fit-content;
            width: -moz-fit-content;
            width: fit-content;  
   	 	}
   	 	.deviceDiv{
           width:$deviceDivWidth;
           cursor:pointer;
           height:auto;
           margin: 1.5em 1.5em 0.5em 1.5em;
           float:left;
           .content{
           	  height: $deviceDivWidth;
           	  border-radius:0.2em;
           	  background: rgba(8,30,66, 1);
           	  width:100%;
           	  border:1.5px solid white;
              box-shadow: 0 0 10px rgba(255,255,255,0.5);
              img{
              	margin-top:1em;
              	width:2.25em;
              	height:2.25em
              }
              img.centerImg{
              	margin-top:1.8em
              }
              span{
              	width:100%;
              	display:block;
              	font-size:0.8em;
              }
              span.centerSpan{
               	margin:0.4em 0 1em 0;
              }
              .hasPercent{
                margin-top:1em;
                position:relative;
                left:calc((100% - 3.2em) / 2);
                width: 3.2em;
                height: 2.57em;
              }
              div.imgLeft{
                 width:0;
                 height:100%;
                 top:0;
                 left:0;
                 position:absolute;
                 background: rgba(99,230,142, 1);
                 z-index: 1;
               //  animation:mymoveL 5s infinite;
               //  -webkit-animation:mymoveL 5s infinite; /*Safari and Chrome*/
              }

              @keyframes mymoveL
               {
                 from {width:0;}  to {width:100%}
               }
              @-webkit-keyframes mymoveL 
               {
                 from {width:0;}  to {width:100%}
               }
              div.imgRight{
                 width: 100%;
                 height:100%;
                 top:0;
                 right:0;
                 background: white;
                 position:absolute;
                 z-index: 1;
                 // animation:mymoveR 5s infinite;
                 //-webkit-animation:mymoveR 5s infinite; /*Safari and Chrome*/
              }
              @keyframes mymoveR
               {
                 from {width:100%;}  to {width:0}
               }
              @-webkit-keyframes mymoveR 
               {
                 from {width:100%;}  to {width:0%}
               }
              div.imgTop{
                 position:absolute;
                 z-index: 2;
                 width:100%;
                 height:100%;
                 background: url('../../../../dist/img/widsomApp/dpc.png') no-repeat;
                 background-size:cover;
              }
              div.imgTopDDC{
                  background-image: url('../../../../dist/img/widsomApp/ddc.png') ;
              }    
           }
           .noUse{
               background: rgba(48,72,109, 1)
            }
            .inDanger{
              border-color: rgba(238,86,72, 1)
            }
           p{
           	  font-size:0.84em;
              margin-bottom: 0;
              width: 100%;
              text-overflow: ellipsis;
              white-space: nowrap;
              overflow: hidden;
           }
   	 	}
   	 }
   	 .tabCon:after, .autoTab:after {
   	 	content: '';
   	 	display:block;
   	 	width:1px;
   	 	height:0;
   	 	clear: both;
   	 }
   }
</style>


